.addon {
  display: contents;
  display: flex;
  flex-direction: column;
  width: 400px;
  //justify-content: space-between;
  font-size: 14px;

  .dateSelect {
    font-family: Open Sans,sans-serif;
    font-size: 13px;
    border-radius: 3px;
    box-shadow: inset 0 2px 2px #e9e9e9;
    border: 1px solid #aeaeae;
    line-height: 15px;
    padding: 5px 10px 5px;
  }

  .input {
    font-family: Open Sans,sans-serif;
    font-size: 13px;
    width: 160px;
  }
  
  .title {
    margin: 10px 0;
    font-weight: bold;
  }
}