.header {
  height: 60px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;

  .logo-container {
    width: 30%;
    height: 100%;
    width: 270px;
    padding: 5px;
  }

  .fold-icon {
    width: 10%;
    height: 24px;
    justify-content: flex-start;
    padding: 20px;
  }

  .options {
    width: 60%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-end;

    .option {
      padding: 10px 15px;
      cursor: pointer;
    }
  }
}
