.k-scatter-popup.panel {
  background-color: white !important;
  position: absolute;
  border: 1px solid #cccccc;
  pointer-events: all;
  padding: 5px !important;
  min-width: 150px;
}

.k-scatter-popup .panel-heading {
  padding: 0px !important;
  float: right;
}

.k-scatter-popup .panel-body {
  max-height: 300px;
  /* overflow: auto; */
  padding: 5px 5px 10px !important;
  font-size: 11px;
}

.k-scatter-popup .panel-body.k-popup-loading {
  display: flex;
  align-items: center;
  justify-content: center;
  color: #999;
}

.k-scatter-popup .panel-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 5px !important;
  font-size: 10px;
}

.k-scatter-popup .k-scatter-view-button {
  text-decoration: none;
  text-shadow: none;
  border: 0;
  background: transparent;
  padding: 0px;
}

.k-scatter-popup .k-scatter-view-button[disabled] {
  color: #999;
  opacity: 0.6;
}

.k-scatter-popup .k-scatter-view-index {
  flex-grow: 1;
  text-align: center;
}

/* All arrows */

.k-arrow-top-left:after,
.k-arrow-top-left:before,
.k-arrow-top-right:after,
.k-arrow-top-right:before,
.k-arrow-right-top:after,
.k-arrow-right-top:before,
.k-arrow-right-bottom:after,
.k-arrow-right-bottom:before,
.k-arrow-left-top:after,
.k-arrow-left-top:before,
.k-arrow-left-bottom:after,
.k-arrow-left-bottom:before,
.k-arrow-bottom-left:after,
.k-arrow-bottom-left:before,
.k-arrow-bottom-right:after,
.k-arrow-bottom-right:before {
  border: solid transparent;
  content: " ";
  height: 0;
  width: 0;
  position: absolute;
  pointer-events: none;
}

.k-arrow-top-left:after,
.k-arrow-top-right:after,
.k-arrow-right-top:after,
.k-arrow-right-bottom:after,
.k-arrow-left-top:after,
.k-arrow-left-bottom:after,
.k-arrow-bottom-left:after,
.k-arrow-bottom-right:after {
  border-color: rgba(255, 255, 255, 0);
  border-width: 10px;
}

.k-arrow-top-left:before,
.k-arrow-top-right:before,
.k-arrow-right-top:before,
.k-arrow-right-bottom:before,
.k-arrow-left-top:before,
.k-arrow-left-bottom:before,
.k-arrow-bottom-left:before,
.k-arrow-bottom-right:before {
  border-color: rgba(153, 153, 153, 0);
  border-width: 11px;
}

/* Arrow Top Left/Right */

.k-arrow-top-left:after,
.k-arrow-top-right:after,
.k-arrow-top-left:before,
.k-arrow-top-right:before {
  bottom: 100%;
  /* left: 50%; */
}

.k-arrow-top-left:after,
.k-arrow-top-left:before {
  left: 10px;
}

.k-arrow-top-right:after,
.k-arrow-top-right:before {
  right: 10px;
}

.k-arrow-top-left:after,
.k-arrow-top-right:after {
  border-bottom-color: #ffffff;
  margin-left: -10px;
}
.k-arrow-top-left:before,
.k-arrow-top-right:before {
  border-bottom-color: #cccccc;
  margin-left: -11px;
}

/* Arrow Right */

.k-arrow-right-top:after,
.k-arrow-right-top:before,
.k-arrow-right-bottom:after,
.k-arrow-right-bottom:before {
  left: 100%;
  /* top: 50%; */
}

.k-arrow-right-top:after,
.k-arrow-right-top:before {
  top: 10px;
}

.k-arrow-right-bottom:after,
.k-arrow-right-bottom:before {
  bottom: 10px;
}

.k-arrow-right-top:after,
.k-arrow-right-bottom:after {
  border-left-color: #ffffff;
  margin-top: -10px;
}
.k-arrow-right-top:before,
.k-arrow-right-bottom:before {
  border-left-color: #cccccc;
  margin-top: -11px;
}

/* Arrow Left */

.k-arrow-left-top:after,
.k-arrow-left-top:before,
.k-arrow-left-bottom:after,
.k-arrow-left-bottom:before {
  right: 100%;
  /* top: 50%; */
}

.k-arrow-left-top:after,
.k-arrow-left-top:before {
  top: 10px;
}

.k-arrow-left-bottom:after,
.k-arrow-left-bottom:before {
  bottom: 10px;
}

.k-arrow-left-top:after,
.k-arrow-left-bottom:after {
  border-right-color: #ffffff;
  margin-top: -10px;
}
.k-arrow-left-top:before,
.k-arrow-left-bottom:before {
  border-right-color: #cccccc;
  margin-top: -11px;
}

/* Arrow Bottom */

.k-arrow-bottom-left:after,
.k-arrow-bottom-left:before,
.k-arrow-bottom-right:after,
.k-arrow-bottom-right:before {
  top: 100%;
  /* left: 50%; */
}

.k-arrow-bottom-left:after,
.k-arrow-bottom-left:before {
  left: 10px;
}

.k-arrow-bottom-right:after,
.k-arrow-bottom-right:before {
  right: 10px;
}

.k-arrow-bottom-left:after,
.k-arrow-bottom-right:after {
  border-top-color: #ffffff;
  margin-left: -10px;
}
.k-arrow-bottom-left:before,
.k-arrow-bottom-right:before {
  border-top-color: #cccccc;
  margin-left: -11px;
}
