.condition-hidden{
  display: none;
}
.condition {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  //justify-content: space-between;

  .options{
    width: 80%;


    .items {
      width: 100%;
      display: flex;
      align-items: flex-start;
      justify-content: center;
      padding: 0px 15px;
      
      .item {
        width: 25%;
        flex-direction: column;

        .title{
          font-weight: bold;
        }
      }
    }
    .buttons {
      justify-content: center; 
      display: flex;
      padding: 10px 50px;
 //     justify-content: space-between;

    }  
  }
  .addon{
    width: 20%;
    flex-direction: column;
    font-weight: bold;
  }
}

