.tick line {
  opacity: 0.1;
}

.domain {
  opacity: 0.1;
}

.k-scatterplot-button {
  display: block;
  z-index: 1001;
  padding: 2px 10px;
  margin: 2px 5px 8px 0px;
  background-color: #dddddd;
  cursor: pointer;
  text-align: center;
  font-size: 12px;
  border: 1px solid #cccccc;
  width: 10em;
}


.kinetica_scatter .slice_container {
  overflow: hidden !important;
  padding: 0px !important;
}

.kinetica_scatter .error-message {
  clear: both;
  padding: 10px 15px;
  background-color: #FFF3E0;
  color: #FFA726;
  margin-bottom: 10px;
}
.kinetica_scatter .axis path,
.kinetica_scatter .axis line {
  fill: none;
  stroke: #999999;
  stroke-width: 0.5;
}
.kinetica_scatter text {
  font-size: 12px;
  fill: #666666;
}
.kinetica_scatter .tick text {
  font-size: 11px;
  fill: #999999;
}
.kinetica_scatter .tick line {
  stroke: #999999;
  stroke-width: 0.5;
}
